import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@material-ui/core';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import ContestNewForm from '../components/campaign/contest/ContestNewForm';

// ----------------------------------------------------------------------

function ContestCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  const currentContest = {};

  return (
    <Page title="Contest: Create a new contest">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Create Contest' : 'Edit Contest'}
          links={[{ name: 'Contest', href: PATH_DASHBOARD.contest.list }, { name: !isEdit ? 'Create Contest' : name }]}
        />
        <ContestNewForm isEdit={isEdit} currentContest={currentContest} />
      </Container>
    </Page>
  );
}

export default ContestCreate;
