import PropTypes from 'prop-types';
import { Add as AddIcon, Delete as DeleteIcon } from '@material-ui/icons';
import { IconButton, Stack, TextField } from '@material-ui/core';

const DynamicFormItem = ({ label, fieldProps, error, helperText, isLastItem, onAddClick, onRemoveClick }) => (
  <Stack direction="row" spacing={1}>
    <TextField fullWidth size="small" label={label} {...fieldProps} error={error} helperText={helperText} />
    {isLastItem ? (
      <IconButton color="primary" onClick={onAddClick}>
        <AddIcon />
      </IconButton>
    ) : (
      <IconButton color="error" onClick={onRemoveClick}>
        <DeleteIcon />
      </IconButton>
    )}
  </Stack>
);

DynamicFormItem.propTypes = {
  label: PropTypes.string,
  fieldProps: PropTypes.object,
  error: PropTypes.bool,
  helperText: PropTypes.string,
  isLastItem: PropTypes.bool,
  onAddClick: PropTypes.func,
  onRemoveClick: PropTypes.func,
};

export default DynamicFormItem;
