import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useSnackbar } from 'notistack5';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import { LoadingButton } from '@material-ui/lab';
import { Box, Card, Grid, Stack, Button } from '@material-ui/core';
// paths
import { PATH_DASHBOARD } from '../../../routes/paths';
// utils
import { nameRegExp } from '../../../utils/constants/regexs';
// components
import Steps from '../../Steps';
import CampaignTypeForm from '../CampaignTypeForm';
import ContestAndRewardsForm from './ContestAndRewardsForm';
import ContestDetailForm from './ContestDetailForm';

// ----------------------------------------------------------------------

const STEPS = ['Campaign Type', 'Contest & Rewards', 'Contest Detail'];

const CampaignTypeSchema = Yup.object().shape({
  brand: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Brand is required'),
  campaignType: Yup.string(),
});

const ContestAndRewardsSchema = Yup.object().shape({
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  contestType: Yup.string(),
  totalBudget: Yup.number(),
  winnerToken: Yup.string(),
  starPerUser: Yup.number(),
  continueAfterBudget: Yup.string(),
  tier: Yup.string(),
  isPublic: Yup.boolean().required('Status is required'),
});

const ContestDetailSchema = Yup.object().shape({
  headerImage: Yup.mixed(),
  description: Yup.string(),
  doList: Yup.array().of(Yup.string()),
  dontList: Yup.array().of(Yup.string()),
  igCaptionQuestions: Yup.array().of(Yup.string()),
  noOfPhotoRequired: Yup.number(),
  noOfVideoRequired: Yup.number(),
  hashtags: Yup.array().of(Yup.string()),
  mentions: Yup.array().of(Yup.string()),
  contestStartDate: Yup.date().nullable(true),
  contestEndDate: Yup.date().when('contestStartDate', (contestStartDate, schema) =>
    contestStartDate ? schema.min(contestStartDate, 'End date must be later than start date') : schema.nullable(true)
  ),
  votingStartDate: Yup.date().nullable(true),
  votingEndDate: Yup.date().when('votingStartDate', (votingStartDate, schema) =>
    votingStartDate ? schema.min(votingStartDate, 'End date must be later than start date') : schema.nullable(true)
  ),
  winnerDate: Yup.date().nullable(true),
});

const VALIDATION_SCHEMAS = [CampaignTypeSchema, ContestAndRewardsSchema, ContestDetailSchema];

function ContestNewForm({ isEdit, currentContest }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [activeStep, setActiveStep] = useState(0);

  const isLastStep = () => activeStep === STEPS.length - 1;

  const handlePrevStep = () => setActiveStep((prev) => prev - 1);

  const handleNextStep = () => setActiveStep((prev) => prev + 1);

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      brand: currentContest?.brand || '',
      campaignType: currentContest?.type || '',
      name: currentContest?.name || '',
      contestType: currentContest?.contestType || '',
      totalBudget: currentContest?.totalBudget || undefined,
      winnerToken: currentContest?.winnerToken || '',
      starPerUser: currentContest?.starPerUser || undefined,
      continueAfterBudget: currentContest?.continueAfterBudget || '',
      tier: currentContest?.tier || '',
      isPublic: currentContest?.isPublic || false,
      headerImage: currentContest?.headerImage || undefined,
      doList: currentContest?.doList || [''],
      dontList: currentContest?.dontList || [''],
      igCaptionQuestions: currentContest?.igCaptionQuestions || [''],
      noOfPhotoRequired: currentContest?.noOfPhotoRequired || undefined,
      noOfVideoRequired: currentContest?.noOfVideoRequired || undefined,
      hashtags: currentContest?.hashtags || [],
      mentions: currentContest?.mentions || [],
      contestStartDate: currentContest?.contestStartDate ? new Date(currentContest.contestStartDate) : null,
      contestEndDate: currentContest?.contestEndDate ? new Date(currentContest.contestEndDate) : null,
      votingStartDate: currentContest?.votingStartDate ? new Date(currentContest.votingStartDate) : null,
      votingEndDate: currentContest?.votingEndDate ? new Date(currentContest.votingEndDate) : null,
      winnerDate: currentContest?.winnerDate ? new Date(currentContest.winnerDate) : null,
    },
    validationSchema: VALIDATION_SCHEMAS[activeStep],
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      if (isLastStep()) {
        try {
          alert(JSON.stringify(values));
          resetForm();
          setSubmitting(false);
          enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
          navigate(PATH_DASHBOARD.contest.list);
        } catch (error) {
          console.error(error);
          setSubmitting(false);
          setErrors(error);
        }
      } else {
        handleNextStep();
      }
    },
  });

  const { errors, values, handleSubmit, isSubmitting } = formik;

  console.log('Active Step: ', activeStep);
  console.log('*** ', values, errors);

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container justifyContent="center">
          <Grid item xs={12} md={8} sx={{ mb: 5 }}>
            <Steps steps={STEPS} activeStep={activeStep} />
          </Grid>
        </Grid>

        <Card sx={{ p: 3 }}>
          <Stack spacing={3}>
            {activeStep === 0 && <CampaignTypeForm formik={formik} />}
            {activeStep === 1 && <ContestAndRewardsForm formik={formik} />}
            {activeStep === 2 && <ContestDetailForm formik={formik} />}
            <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
              {activeStep !== 0 && (
                <Button
                  sx={{ mr: 2 }}
                  variant="outlined"
                  onClick={isLastStep() ? () => console.log('Draft Clicked') : handlePrevStep}
                >
                  {isLastStep() ? 'Draft' : 'Back'}
                </Button>
              )}
              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {isLastStep() ? 'Setup Complete' : 'Next'}
              </LoadingButton>
            </Box>
          </Stack>
        </Card>
      </Form>
    </FormikProvider>
  );
}

ContestNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentContest: PropTypes.object,
};

export default ContestNewForm;
