import React from 'react';
import PropTypes from 'prop-types';
import { useTheme } from '@material-ui/core/styles';
import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  FormControlLabel,
  Grid,
  Radio,
  RadioGroup,
  Stack,
  TextField,
} from '@material-ui/core';

const CampaignTypeForm = ({ formik }) => {
  const theme = useTheme();
  const { errors, values, touched, getFieldProps } = formik;
  return (
    <Grid container spacing={3}>
      <Grid item md={12}>
        <Stack spacing={3}>
          <Grid container>
            <Grid item xs={12} md={4}>
              <TextField
                select
                fullWidth
                size="small"
                label="Select Brand"
                placeholder="Brand"
                {...getFieldProps('brand')}
                SelectProps={{ native: true }}
                error={Boolean(touched.brand && errors.brand)}
                helperText={touched.brand && errors.brand}
              >
                <option value="" />
                {['Adidas', 'Nike'].map((option, index) => (
                  <option key={option + index} value={option}>
                    {option}
                  </option>
                ))}
              </TextField>
            </Grid>
          </Grid>

          <RadioGroup {...getFieldProps('campaignType')} value={values.campaignType}>
            <Grid container spacing={1.5} direction="row" columns={10}>
              {['Single Task', 'Voting and Winner', 'Question and Answer', 'Watch Ad', 'Campaign'].map((type) => {
                const isSelected = values.campaignType === type;
                return (
                  <Grid item xs={5} md={2} key={type}>
                    <Card
                      variant={isSelected ? 'elevation' : 'outlined'}
                      sx={{
                        ...(isSelected && {
                          border: `solid 2px ${theme.palette.primary.main}`,
                        }),
                      }}
                    >
                      <CardMedia
                        sx={{ height: 140 }}
                        image="/static/mock-images/products/product_1.jpg"
                        title="Contemplative Reptile"
                      />
                      <CardContent>{type}</CardContent>
                      <CardActions>
                        <FormControlLabel
                          label=""
                          value={type}
                          control={<Radio sx={{ display: 'none' }} />}
                          sx={{
                            top: 0,
                            margin: 0,
                            width: '100%',
                            height: '100%',
                            position: 'absolute',
                          }}
                        />
                      </CardActions>
                    </Card>
                  </Grid>
                );
              })}
            </Grid>
          </RadioGroup>
        </Stack>
      </Grid>
    </Grid>
  );
};

CampaignTypeForm.propTypes = {
  formik: PropTypes.object,
};

export default CampaignTypeForm;
